
.navigation {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: auto;
}

.navigation a {
  color: black;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Raleway', sans-serif;
}

.bar {
  width: 25px;
  height: 4px;
  background-color: #343239;
  margin: 0px 0 4px 20px;
}

.mobile-menu li {
  text-decoration: none;
  padding: 0 10px 10px 10px;
  list-style: none;
}

.mobile-menu {
  margin: 0 auto auto 55px;
  opacity: 0;
  display: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 0;
  position: absolute;
}

.hamburger-checkbox {
  display: none;
}

.hamburger-checkbox:checked + .hamburger-mobile .bar-one {
  -webkit-transform: rotate(-45deg) translate(-6px, 5px);
      -ms-transform: rotate(-45deg) translate(-6px, 5px);
          transform: rotate(-45deg) translate(-6px, 5px);
  -webkit-transition: all 0.6s ease-in-out;
  -o-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.hamburger-checkbox:checked + .hamburger-mobile .bar-two {
  opacity: 0;
}

.hamburger-checkbox:checked + .hamburger-mobile .bar-three {
  -webkit-transform: rotate(45deg) translate(-5px, -5px);
      -ms-transform: rotate(45deg) translate(-5px, -5px);
          transform: rotate(45deg) translate(-5px, -5px);
  -o-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
  -moz-transition: all 0.6s ease-in-out;
  -webkit-transition: all 0.6s ease-in-out;
}

.hamburger-checkbox:checked ~ .mobile-menu {
   opacity: 1;
   -o-transition: opacity .8s ease-in-out;
   transition: opacity .8s ease-in-out;
   -moz-transition: opacity .8s ease-in-out;
   -webkit-transition: opacity .8s ease-in-out;
   }

@media (max-width: 320px) {
    .mobile-menu {
      -ms-flex-direction: column;
          flex-direction: column;
      line-height: 1;
    }
   }



